.contact-wrap{position:absolute;top:50%;left:50%;display:flex;align-items:center;justify-content:center;width:90vw;height:80vh;padding:40px 40px;border-radius:10px;box-sizing:border-box;transform:translate(-50%, -50%);background-color:#fff;overflow:hidden}.contact-wrap .contact-info{display:block;width:40%}.contact-wrap .contact-info h2{display:block;margin-bottom:15px;font-family:"Galmuri11",Apple SD Gothic Neo,돋움,dotum,sans-serif;font-weight:bolder;font-size:50px;color:#593b22}.contact-wrap .contact-info p{font-size:18px;color:#333}.contact-wrap .contact-letter{display:block;width:60%}.contact-wrap input[type=submit]{display:block;width:100%;height:50px;margin-top:20px;border-radius:999px;background-color:#593b22;color:#fff}.input-wrap+.input-wrap{margin-top:20px}.input-wrap label{display:block;width:100%;margin-bottom:10px;font-size:16px;font-family:"Galmuri11",Apple SD Gothic Neo,돋움,dotum,sans-serif;color:#333}.input-wrap input{display:block;width:100%;height:40px;padding:10px 15px;border:0;border-radius:10px;box-sizing:border-box;background-color:#dbcdc1;overflow:hidden}.input-wrap textarea{display:block;width:100%;min-height:300px;padding:10px 15px;border:0;border-radius:10px;box-sizing:border-box;background-color:#dbcdc1;resize:none}@media(max-width: 1000px){.contact-wrap{flex-direction:column;align-items:flex-start;justify-content:flex-start;width:90vw;height:90vh;margin-top:20px;padding:40px 20px;border-radius:15px;overflow:auto}.contact-wrap .contact-info{width:100%;margin-bottom:40px}.contact-wrap .contact-info h2{font-size:32px}.contact-wrap .contact-info p{font-size:14px}.contact-wrap .contact-letter{width:100%}.input-wrap label{font-size:13px}.input-wrap input{border-radius:5px}.input-wrap textarea{min-height:150px;border-radius:5px}}